import axios from 'axios'

export default ({
  namespaced: true,
  state: {
    publicAddress: '',
    token: null,
    user: null,
    username: "mariamiko",
    biography: null,
    instagram: "aehaa",
    twitter: ""
  },
  mutations: {
    setToken (state, data) {
        state.token = data.token;
        console.log(data);
    },
    logout (state) {
        state.token = null;
        state.publicAddress = null;
    },
    setAddress (state, data) {
        state.publicAddress = data;
    },
    updateUser (state, data) {
      if(data.username !== undefined) {
        state.username = data.username;
      }
      if(data.bio !== undefined) {
        state.biography = data.bio;
      }
      if(data.instagram !== undefined) {
        state.instagram = data.instagram;
      }
      if(data.twitter !== undefined) {
        state.twitter = data.twitter;
      }
    }
  },
  actions: {
    async signIn({commit, dispatch}){
        const accounts = await ethereum.request({ method: 'eth_requestAccounts' });
        const publicAddress = accounts[0];
        const res = await axios.get('https://api.nftslab.io/api/v1/authenticate/' + publicAddress );
        this.nonce = res.data.nonce;
        this.publicAddress = publicAddress;
        this.commit("auth/setAddress", publicAddress);
        console.log("nonce:");
        console.log(this.nonce);
        ethereum
            .request({"method": "personal_sign", "params": [this.nonce, publicAddress]})
            .then((result) => {
                // The result varies by RPC method.
                // For example, this method will return a transaction hash hexadecimal string on success.
                // console.log(result);
                this.signature = result;
                console.log(this.signature)
                dispatch("login");
                dispatch("getUserInfo");

            })
            .catch((error) => {
                // If the request fails, the Promise will reject with an error.
            });  
      },
    getUserInfo({commit}) {
      axios.get('https://api.nftslab.io/api/v1/users/' + this.state.auth.publicAddress)
      .then((response) => {
       
         this.commit("auth/updateUser", response.data.user);
      })
      axios.get('https://api.nftslab.io/api/v1/users/' + this.state.auth.publicAddress + "/socials")
      .then((response) => {
          let objects = response.data.socials;
          console.log("test:");
          let instagram = getSocial("instagram", objects);
          let twitter = getSocial("twitter", objects);
          let user = {}
          if(twitter !== undefined) {
             user.twitter = twitter
          }
          if(instagram !== undefined) {
             user.instagram = instagram
          }
          console.log(user);

         this.commit("auth/updateUser", user);
      })
    },
    async login({commit}) {
        // let json = JSON.stringify({ signature: this.signature })
        let params = {"signature": this.signature, "nonce": this.nonce};
        console.log('signature:');
        console.log(params);
        const resp = await axios.post('https://api.nftslab.io/api/v1/login/' + this.publicAddress, params)
        .then( (response) => {
            console.log('response:');
            console.log(response.data);
            if (response.data.token.length > 0 ){
                // this.token = response.data.token;
                // console.log(this);
                this.commit({type: "auth/setToken", token: response.data.token});
                axios.defaults.headers.common['Authorization'] = "Bearer "+response.data.token ;
            }
        }) 
    },
  }
})


function getSocial(toSearch, objects) {

  for(var i=0; i<objects.length; i++) {
    if(objects[i].name.toLowerCase() == toSearch.toLowerCase()) {
      return objects[i];
    }
  }

}