import Vuex from 'vuex'
import { createStore } from 'vuex'
import auth from './auth'

export default createStore({
  state: {
    test: null,
    headerLight:true
  },
  mutations: {
    darkHeader (state) {
      state.headerLight = false;
  },
    lightHeader (state) {
      state.headerLight = true;
  },
  },
  actions: {

  },
  modules: {
    auth
  }
})
