<template>
  <div class="no-bottom no-top" id="content">
    <div id="top"></div>

    <!-- section begin -->
    <section
      id="subheader"
      class="text-light"
      data-bgimage="url(images/background/subheader.jpg) top"
    >
      <div class="center-y relative text-center">
        <div class="container">
          <div class="row">
            <div class="col-md-12 text-center">
              <h1>Profile</h1>
            </div>
            <div class="clearfix"></div>
          </div>
        </div>
      </div>
    </section>
    <!-- section close -->

    <!-- section begin -->
    <section aria-label="section">
      <div class="container">
        <div class="row wow fadeIn">
          <div class="col-lg-7 offset-lg-1">
            <form
              id="form-create-item"
              class="form-border"
              method="post"
              action="email.php"
            >
              <div class="field-set">
                <h5>Upload Profile Picture</h5>
                <UploadImages @changed="handleImages" :max="1" id="profile-pic" class="d-create-file"/>
                <div v-if="message">{{ message }}</div>
                <!-- <h5>Upload Profile Picture</h5>

                <div class="d-create-file">
                  <p id="file_name">PNG, JPG or GIF. Max 2MB.</p>
                  <input
                    type="button"
                    id="get_file"
                    class="btn-main"
                    value="Browse"
                  />
                  <input type="file" id="upload_file" />
                </div> -->

                <div class="de_tab tab_methods">
                  <div class="de_tab_content">
                    <div id="tab_opt_1">
                      <h5>Username</h5>
                      <input
                        type="text"
                        name="user_name"
                        id="user_name"
                        v-model="userName"
                        class="form-control"
                        placeholder="enter username"
                      />
                    </div>
                  </div>
                </div>

                <h5>BIO</h5>
                <textarea
                  data-autoresize
                  name="biography"
                  id="biography"
                  v-model="bio"
                  class="form-control"
                  placeholder="Tell us about yourself"
                ></textarea>

                <div class="spacer-10"></div>

                <input
                  type="button"
                  id="submit"
                  class="btn-main"
                  value="Update Profile"
                  @click="updateProfile"
                />
              </div>
            </form>
          </div>

          <div class="col-lg-3 col-sm-6 col-xs-12">
            <h5>Socials</h5>
            <div id="socials">
              <h5>Instagram</h5>
              <input
                type="text"
                v-model="insta"
                name="instagram"
                id="instagram"
                class="form-control"
                placeholder="enter instagram"
              />
              <div class="spacer-single"></div>
              <h5>Twitter</h5>
              <input
                type="text"
                name="twitter"
                v-model="twitt"
                id="twitter"
                class="form-control"
                placeholder="enter twitter"
              />
            </div>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<script>

import axios from "axios";
import UploadImages from "vue-upload-drop-images"

export default {
    components: {
      UploadImages,
    },
    data: function() {
        return {
          userName: "",
          bio: "",
          twitt: "",
          insta: "",
          image: null,
          message: "",
        }
    },
    computed: {
        Username : function(){
          return this.$store.state.auth.username
        },
        Biography : function(){
          return this.$store.state.auth.biography
        },
        twitter : function(){
          return this.$store.state.auth.twitter
        },
        instagram : function(){
          return this.$store.state.auth.instagram
        },
    },
    mounted() {
        this.$store.commit("darkHeader");
        this.userName = this.$store.state.auth.username;
        this.bio =  this.$store.state.auth.biography;
        this.twitt = this.$store.state.auth.twitter.username;
        this.insta = this.$store.state.auth.instagram.username;
        document.getElementsByClassName('mainMessage')[0].innerHTML = "PNG, JPG or GIF, Max 2MB & 500x500px "; 
    },
    unmounted() {
        this.$store.commit("lightHeader");
    },
    methods: {
        updateProfile(){
          if(this.userName !== this.$store.state.auth.username || this.bio !== this.$store.state.auth.biography){
                  axios.patch('https://api.nftslab.io/api/v1/users/' + this.$store.state.auth.publicAddress, 
                  {
                    username: this.userName,
                    bio: this.bio,
                  })
                  .then((response) => {
                    this.$store.dispatch("auth/getUserInfo");
                  })
          }

          if (this.$store.state.auth.twitter.username.length > 0 && this.twitt !== this.$store.state.auth.twitter.username){
            axios.patch('https://api.nftslab.io/api/v1/users/' + this.$store.state.auth.publicAddress + "/socials/" + this.$store.state.auth.twitter.id, 
                  {
                    name: "Twitter",
                    username: this.twitt
                  })
                  .then((response) => {
                    this.$store.dispatch("auth/getUserInfo");
          
             })
          }else if(!this.$store.state.auth.twitter.username.length && this.twitt) {
           axios.put('https://api.nftslab.io/api/v1/users/' + this.$store.state.auth.publicAddress + "/socials" , 
                  {
                    name: "Twitter",
                    username: this.twitt
                  })
                  .then((response) => {
                    this.$store.dispatch("auth/getUserInfo");
          
             })
          };
          if (this.$store.state.auth.instagram.username.length > 0 && this.insta !== this.$store.state.auth.instagram.username){
            axios.patch('https://api.nftslab.io/api/v1/users/' + this.$store.state.auth.publicAddress + "/socials/" + this.$store.state.auth.instagram.id, 
                  {
                    name: "Instagram",
                    username: this.insta
                  })
                  .then((response) => {
                    this.$store.dispatch("auth/getUserInfo");
          
             })
          }else if(!this.$store.state.auth.instagram.username.length && this.insta) {
           axios.put('https://api.nftslab.io/api/v1/users/' + this.$store.state.auth.publicAddress + "/socials" , 
                  {
                    name: "Instagram",
                    username: this.insta
                  })
                  .then((response) => {
                    this.$store.dispatch("auth/getUserInfo");
          
             })
          }
          console.log("imigii " + this.image);
          if (this.image){
            var formData = new FormData();
            formData.append("image", this.image)
            axios.post('https://api.nftslab.io/api/v1/users/' + this.$store.state.auth.publicAddress + "/picture", 
                  formData, {
                    headers: {
                      'Content-Type': 'multipart/form-data'
                    }
                  })
                  .then((response) => {
                    console.log(response);
          
             })
          }
          
        },

         handleImages(files){ 
           if(files[0]) {
              if(files[0].size < 2097152 && (files[0].type !== "image/png" ||  files[0].type !== "image/jpeg" || files[0].type !== "image/gif") ){
                this.image = files[0];
                this.message= "";
              }
              else {
                this.message="file format should be png, jpg or gif, file size shouldn't be more than 2MB"
              }
          }
         }
    }
};
</script>

<style>

.imgsPreview .imageHolder .plus[data-v-69bb59a3] {
  display: none !important;
}
.container[data-v-69bb59a3] {
  border: dashed 3px #dddddd ;
  background-color: #fff;
}
.beforeUpload .icon[data-v-69bb59a3] {
  filter: invert(40%) sepia(85%) saturate(1791%) hue-rotate(230deg) brightness(93%) contrast(89%);
}
</style>